import { styled } from "@mui/system";

export const Container = styled("div")({
  position: "relative",
  width: "100%",
  minHeight: "100vh",
  background: "#FFFFFF",
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "flex-start",
});

export const Logo = styled("img")({
  width: "200px",
  height: "auto",
  marginBottom: "20px",
});

export const Heading = styled("h2")({
  fontFamily: "Roboto",
  fontStyle: "normal",
  backgroundColor: "#00a19B",
  color: "#fff",
  border: "none",
  borderRadius: "20px",
  padding: "10px 40px",
  fontSize: "12px",
  fontWeight: "bold",
  cursor: "pointer",
});

export const BackButton = styled("button")({
  position: "absolute",
  top: "20px",
  left: "20px",
  padding: "10px",
  background: "none",
  border: "none",
  cursor: "pointer",
});

export const BackIcon = styled("img")({
  width: "24px",
  height: "24px",
});

export const DropdownContainer = styled("div")({
  width: "100%",
  background: "#FFFFFF",
  borderRadius: "16px",
});

export const Dropdown = styled("div")({
  marginBottom: "10px",
  width: "100%",
});

export const DropdownHeading = styled("div")({
  fontFamily: "Roboto",
  fontStyle: "normal",
  fontWeight: 500,
  fontSize: "10px",
  lineHeight: "160%",
  color: "#000000",
  marginBottom: "4px",
});

export const DropdownHeader = styled("div")({
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  padding: "5px",
  border: "1px solid #E0E0E0",
  borderRadius: "8px",
  cursor: "pointer",
  fontFamily: "Roboto",
  fontStyle: "normal",
  fontWeight: 400,
  fontSize: "10px",
  lineHeight: "160%",
  color: "#000000",
});

export const Arrow = styled("span")({
  fontSize: "12px",
});

export const DropdownList = styled("div")({
  padding: "10px",
  background: "#FFF",
  boxShadow: "0px 1px 4.6px rgba(0, 0, 0, 0.15)",
  borderRadius: "8px",
  marginTop: "5px",
});

export const DropdownItem = styled("div")({
  padding: "5px 10px",
  fontFamily: "Roboto",
  fontStyle: "normal",
  fontWeight: 400,
  fontSize: "10px",
  lineHeight: "160%",
  color: "#63666A",
  cursor: "pointer",
  "&:hover": {
    backgroundColor: "#f0f0f0",
  },
});

export const ImportButton = styled("button")({
  padding: "10px 20px",
  fontFamily: "Roboto",
  fontStyle: "normal",
  fontWeight: 400,
  fontSize: "16px",
  color: "#FFFFFF",
  backgroundColor: "#0081C5",
  borderRadius: "8px",
  border: "none",
  cursor: "pointer",
  "&:hover": {
    backgroundColor: "#005f99",
  },
});
