import React, { useEffect, useState } from "react";
import * as excelfucnitons from "./ExcelMidleware/excelFucntions";

const useStyles = makeStyles({
  button: {
    margin: "10px",
    padding: "10px 20px",
    fontSize: "14px",
    cursor: "pointer",
    backgroundColor: "#00a19B",
    color: "#fff",
    border: "none",
    borderRadius: "5px",
    "&:hover": {
      backgroundColor: "#008a7d",
    },
  },
  container: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
  },
});

const DynamicButtonComponent = () => {
  const [uniqueValues, setUniqueValues] = useState([]);
  const classes = useStyles();

  useEffect(() => {
    const fetchData = async () => {
      const values = await excelfucnitons.ACEgetcomlumn6();
      setUniqueValues(values);
    };

    fetchData();
  }, []);

  const handleClick = (value) => {
    console.log(`Button with value ${value} clicked`);
    // Add your specific function logic here
  };

  return (
    <div className={classes.container}>
      {uniqueValues.map((value, index) => (
        <button
          key={index}
          className={classes.button}
          onClick={() => handleClick(value)}
        >
          {value}
        </button>
      ))}
    </div>
  );
};

export default DynamicButtonComponent;
